import { useCallback } from "react";
import { gql } from "../__generated__";
import { useQuery } from "@apollo/client";
import { ICalendar } from "datebook";
import * as FileSaver from "file-saver";

const ALL_STUFF = gql(`query getEventTimetable(
    $orderBy: eventPhaseOrderByEnum
    $orderDirection: OrderDirectionEnum
    $eventId: Int
  ) {
    getEventTimetable(
      orderBy: $orderBy
      orderDirection: $orderDirection
      eventId: $eventId
    ) {
      id
      phaseName
      eventID
      phaseDateAndTime
      sexCode
      unitName
      unitTypeName
      discipline {
        _id
        name
        typeName
        typeOrder
        order
        nameUrlSlug
        __typename
      }
      isStartlistPublished
      isResultPublished
      isPhaseSummaryPublished
      combinedDisciplineOrder
      phaseOrder
      phaseSessionName
      phaseSessionOrder
      status
      isPointsPublished
      phaseNameUrlSlug
      sexNameUrlSlug
      __typename
    }
  }
`);

export const GetCompetitions = () => {
  const { loading, data } = useQuery(ALL_STUFF, {
    variables: { eventId: 7138987 },
  });

  if (loading) {
    return <p>Laddar...</p>;
  }

  if (!data) {
    return <p>Err...</p>;
  }

  const download = () => {

    const cal = data.getEventTimetable?.reduce(
      (prev: undefined | ICalendar, curr) => {
        const event = new ICalendar({
          title: `${curr?.discipline?.name} - ${curr?.phaseName}`,
          location: "Budapest",
          description: `${curr?.phaseSessionName} - ${curr?.discipline?.typeName}`,
          start: new Date(curr?.phaseDateAndTime),
          end: new Date(
            new Date(curr?.phaseDateAndTime).getTime() + 60 * 30 * 1000
          ),
        });
        return prev?.addEvent(event) ?? event;
      },
      undefined
    );
    if (!cal) {
        return;
    }

    const blob = new Blob([cal.render()], {
      type: "text/calendar",
    });

    console.log("RENDER RABLE", cal.render());

    FileSaver.saveAs(blob, "my-calendar-event.ics");
  };

  const sexCodes = new Set(
    data.getEventTimetable?.map((event) => event?.sexCode)
  );
  const phaseName = new Set(
    data.getEventTimetable?.map((event) => event?.phaseName)
  );
  const disciplineTypeName = new Set(
    data.getEventTimetable?.map((event) => event?.discipline?.typeName)
  );
  console.log("SEX CODES", sexCodes);
  console.log("PHASE", phaseName);
  console.log("diciplineTypeName", disciplineTypeName);

  return (
    <>
      <button onClick={download}>Download</button>
      {data?.getEventTimetable?.map((event) => {
        return (
          <>
            <h1>
              {event?.discipline?.name} - {event?.phaseName}
            </h1>
            <p>{event?.phaseDateAndTime}</p>
          </>
        );
      })}
    </>
  );
};
